<template>
          <div class="paperblock">       
             <img :src="moudleData.hitSrc" class="paper" usemap="#PagePicMap">
             <!-- <div class="hit-area" @contextmenu.prevent="addHit($event)"> -->
              <div class="pre" :style="moudleData.style" v-show="moudleData.hitData.length > 0">
                  <vue-draggable-resizable class="hit-area-item" v-for="(hit, index) in moudleData.hitData" :w="hit.w" :h="hit.h" :x="hit.x" :y="hit.y" @activated="onActivated(index)" @deactivated="onDeactivated" @dragging="onDrag" @resizing="onResize" :parent="true" tabindex="1" :key="index">
                    <div @keyup="deleteMe($event)">  热区{{hit.index}}</div>                
                  </vue-draggable-resizable>
              </div>
            <!-- </div> -->
          </div>
</template>

<script>
import VueDraggableResizable from 'vue-draggable-resizable'
export default {
  name: 'hitPicShow',
  props: {
    moudleData: Object,
  },
  components: {
    VueDraggableResizable
  },
  data() {
        return {
             width: 0,
             height: 0,
             x: 0,
             y: 0,
             currentArea:-1
        }
    },
   methods: {
       onActivated(index) {
           this.currentArea=index;
           console.log(this.currentArea)
          },
       onDeactivated () {
            this.currentArea=-1;
          },
       onResize(x, y, width, height) {
           this.moudleData.hitData[this.currentArea].x=x
           this.moudleData.hitData[this.currentArea].y=y
           this.moudleData.hitData[this.currentArea].w=width
           this.moudleData.hitData[this.currentArea].h=height
         },
         onDrag(x, y) {
           this.moudleData.hitData[this.currentArea].x=x
           this.moudleData.hitData[this.currentArea].y=y

      },deleteMe(index){
          console.log(index)
          this.moudleData.hitData.splice(index,1)
      },
      addHit(e){
          var index=0;
          if(this.moudleData.hitData.length>0){
            var length=this.moudleData.hitData.length;
            index=this.moudleData.hitData[length-1].index;
            index++;
          }
        this.moudleData.hitData.push({
            x:e.layerX,
            y:e.layerY,
            w:100,
            h:100,
            link:'',
            linkText:'',
            index:index
        })
        if(this.moudleData.mode=='link'){
          this.moudleData.mode='hit'
        }
      }
  }
}
</script>

