var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"main"},[_c('HitPicShow',{attrs:{"moudleData":_vm.form.moduleData}}),_c('div',{staticClass:"list_content detail-art",staticStyle:{"zoom":"100%"},attrs:{"id":"ozoom","ttsid":"tts"}},[_c('p',{staticClass:"introtitle text-center",attrs:{"id":"PreTitle"}},[_vm._v("围场着力引育人才、构建绿色金融、加强项目建设服务保障助推高质量发展")]),_c('h2',{staticClass:"art-title text-center",attrs:{"id":"Title"}},[_vm._v("创新服务，激活发展新引擎")]),_c('p',{staticClass:"subtitle text-center",attrs:{"id":"SubTitle"}},[_vm._v("河北日报记者 曹 智")]),_vm._m(0),_c('div',{staticClass:"papercontent"},[_c('founder-content',[_c('p',[_vm._v(" 悠悠长江水，浓浓冀丰情。")]),_c('p',[_vm._v(" 1992年，国务院办公厅下发《关于开展对三峡工程库区移民工作对口支援的通知》，要求河北省作为对口支援帮扶单位对库区移民大县重庆市丰都县开展帮扶工作。")]),_c('p',[_vm._v(" 河北省、丰都县，相隔1000多公里的两地从此携手向前。数字显示，河北通过产业项目、基础设施建设等多种方式进行帮扶，累计无偿援助丰都县资金2.276亿元，实施各类帮扶项目185个，受益人口25.1万人，为推动丰都县经济社会发展和乡村振兴贡献了“河北力量”。")]),_c('p',[_vm._v(" 日前，记者跟随省发改委调研组赴丰都县进行了实地探访。")]),_c('p',[_vm._v(" ■ 健全基本公共服务体系，让苦日子成为历史")]),_c('p',[_vm._v(" 2月14日，丰都县花园村，坐在龙河丰都段的滨河绿道旁，80岁的村民陈国奉和老伴感觉非常惬意。他们告诉记者，1992年之后，他们搬过两次家来到这里。河边原本是工厂，比较吵闹。现在，工厂变成了休闲区域，就在自己家门口，没事的时候他们就来公园坐坐。")]),_c('p',[_vm._v(" 丰都县水利局副局长张天绪介绍，龙河滨河绿道属于龙河左岸生态绿道工程。该工程起于丰都县城长江防护大堤东端，止于三合街道童仙寨村两会桥，全长5.6公里，于2020年5月开工，总投资3000万元，其中河北对口支援资金700万元。项目明年建成，将成为连接丰都县城和龙河湿地公园的慢行步道，成为人民群众盼望已久的休闲健身区域。")]),_c('p',[_vm._v(" “谢谢河北对我们的资助，让我们能在舒适的环境中学习……”2月15日上午，阳光洒向大地，给美丽的丰都县滨江小学披上了一件金色外衣，校园愈发显得雅致。五年级六班里，学生们齐声表达对河北对口支援的感谢。")]),_c('p',[_vm._v(" 平整开阔的运动场、焕然一新的教学楼、便捷先进的多媒体、可升降的学习桌、节能护眼的照明设备……该校校长雷渝一一介绍着河北对口支援的建设成果。他说：“滨江小学的师生是河北对口支援的直接受益者，平时我们也会对学生们提及这些，孩子们对河北的感谢发自内心。”")]),_c('p',[_vm._v(" 漫步丰都县城，河北步行街、冀丰建设大厦等含有河北元素的街道、建筑等已经成为当地居民生活的一部分。")]),_c('p',[_vm._v(" “30多年来，河北帮助丰都健全基本公共服务体系，援建中小学校舍、体育场、希望学校、福利院、医疗点等一批民生项目，改善了丰都的办学条件和就医环境，让丰都县三峡移民苦日子成为历史，生活越来越红火。”省发改委对口支援处处长王立刚表示。")]),_c('p',[_vm._v(" 在河北对口支援工作的帮扶下，丰都县建设了移民培训基地（丰都县职教中心）、平都中学冀丰科技大楼、丰都中学体育场和双路、名山、镇江等希望学校，大幅改善了丰都县办学条件；河北援建的福利院、医疗点等公益项目，缓解了基层群众养老难、就医难等问题；河北援建的虎威镇红岩村、高家镇汶溪村、名山镇何家坪村等新农村项目，极大改善了农民群众生产生活条件。")]),_c('p',[_vm._v(" ■ 从“输血”到“造血”，发展产业助民增收")]),_c('p',[_vm._v(" 设施好了，发展的机会多了。")]),_c('p',[_vm._v(" “路通村美，人们眼睛里也就有光了。经过比较，我们选择了经济效益较好的藠头进行种植。藠头成熟后出口韩国等地，供不应求，每亩收入约1万元。村里还有很多致富产业，吸引着原本在外的村民回来发展。”在丰都县建国村，该村驻村第一书记余敬春介绍。")]),_c('p',[_vm._v(" 以前的建国村，土地贫瘠、交通不便，经济条件和人居环境都很落后。“2019年以前，村民大多靠种地、卖鱼苗生活，收入微薄，很多人常年不回家。”该村村民杨爱军说。")]),_c('p',[_vm._v(" 为有效改善村庄落后的基础设施，建设和美乡村，2019年和2020年，当地决定向建国村投入资金，从改善农户的人居环境、饮水安全、住房保障等方面入手，补齐农村公共服务基础设施的“短板”。")]),_c('p',[_vm._v(" 在该村，河北对口支援了1741万元，实施了湿地公园建设、人居环境改善、人畜饮水池建设、人行便道建设、山坪塘整修、渠堰整修、自来水管网安装等7个项目。")]),_c('p',[_vm._v(" 余敬春介绍，这些项目完工后，全村的人居环境有了显著提升，保障了该村1500余人的出行安全，确保了全村2000余人的饮水安全。")]),_c('p',[_vm._v(" 如今，这个往日偏远落后的山村面貌焕然一新。绿水青山、青瓦白墙充分展现了该村“彩农山乡，厚慈建国”的独特文化。该村也被评选为“全国乡村治理示范村”“重庆市乡村振兴市级示范村”“重庆市农村生活垃圾分类示范村”。")]),_c('p',[_vm._v(" “村里环境美了，来的游客多了，我还开了‘坝坝茶’和超市，吃上了旅游饭！”对比以前的生活，杨爱军笑得合不拢嘴，“家里干净，村里漂亮，这样的好日子以前真是不敢想，感谢河北对我们村实打实的付出。”")]),_c('p',[_vm._v(" 更多的产业在丰都县落户。")]),_c('p',[_vm._v(" “2016年，河北投资的蛋鸡良种繁育西南中心项目落户丰都。现在这个项目已经成为西南地区最大的孵化中心，占据西南地区约30%的市场。”在丰都县华裕农业科技有限公司，看着刚完成孵化的蛋鸡，该公司行政经理石现强自豪地说，除了用高科技解决养殖的环保问题，项目还带动了当地6000余名农民从事上下游产业，从“输血”变“造血”，该项目带动了附近蛋鸡养殖相关产业发展，形成了近20亿元产值的产业链。")]),_c('p',[_vm._v(" 中国大唐集团新能源有限责任公司投资30亿元的风力发电项目、河北华裕农业科技有限公司投资4.5亿元的蛋鸡良种繁育等项目已成功落户丰都。这些项目的实施，对丰都县优化产业结构、经济持续健康发展将产生重要的支撑和带动作用。")]),_c('p',[_vm._v(" 省发改委相关负责人介绍，围绕丰都县县域特色农业、民俗文化、商贸旅游、生态工业等资源优势，河北还因地制宜通过举办会展、制作发放宣传资料、发布招商信息等方式引进66个项目，协议投资额近83亿元，为培育壮大丰都各类产业提供了支持，促进了当地群众增收。")]),_c('p',[_vm._v(" ■ 两地互动频繁，积极搭建紧密交流学习平台")]),_c('p',[_vm._v(" “很多第一次来到丰都县城的河北人打开地图都会惊讶：这里有河北步行街。我们解释了河北和丰都的关系后，他们都对这里感到非常亲切。”丰都县副县长李勇说。")]),_c('p',[_vm._v(" 地处美丽的长江之滨，居三峡库区腹心、重庆版图中心的丰都不仅是移民大县，还是国家首批对外开放县、省级历史文化名城、全国优秀旅游城区、全国卫生城市、全国最美生态旅游示范县。该县有1900多年的历史，境内旅游资源富集独特，共有6类100余处旅游资源。因为接待国外游客较多，当地重点景区的导游熟练掌握中英双语讲解技能。")]),_c('p',[_vm._v(" 2018年，为加强河北与丰都两地旅游业间的沟通交流，促进合作共赢，重庆丰都旅游（河北）推介会在石家庄举行，吸引了众多河北游客关注。")]),_c('p',[_vm._v(" “如今，希望更多河北游客来丰都参观游览。”李勇说。今年春节期间，丰都共接待各类游客196.5万人次，旅游综合收入4.83亿元。该县今年结合当地文化举办了祈福文化节，进一步提高了丰都的知名度、社会美誉度。目前，该县重点建设长江历史文化旅游线、龙河峡谷生态旅游线、南天湖高山度假旅游线三条文旅线路，着力打造国际知名文化旅游目的地。")]),_c('p',[_vm._v(" 30多年来，河北深入践行党中央决策部署，通过开展互访交流、挂职锻炼、支医、支教、支农等方式，积极搭建紧密交流学习平台，引领增强造血功能。据统计，河北各级领导和企业家考察团累计176批、980人次到丰都县考察指导工作和洽谈项目，丰都县累计派出160批、810人次赴河北省学习考察。河北还累计为丰都提供干部挂职岗位56人次，培训移民致富带头人1150余人、各类专业技术人员900余人。")]),_c('p',[_vm._v(" 2022年6月，省发改委对口支援调研组来到丰都县实地了解对口支援工作开展情况，并就新一轮支援规划情况进行对接。")]),_c('p',[_vm._v(" 从省发改委获悉，接下来，河北会一如既往支持丰都发展，在加强生态保护和环境治理、支持产业高质量发展、提高基本公共服务和民生保障能力、推进库区城乡区域协调发展、提升库区对外开放合作水平等方面发力，进一步加强对口帮扶、深化结对合作、落实好资金支持，全力以赴做好对口支援工作，着力提升支援合作水平和效益，助力丰都高质量发展。 ")])])],1)]),_c('div',{staticClass:"aside"},[_c('el-form',{ref:"form",attrs:{"model":_vm.form,"label-width":"80px","label-position":"left"}},[_c('el-form-item',{attrs:{"label":"上传图片"}},[_c('el-upload',{staticClass:"upload-demo",attrs:{"action":"https://jsonplaceholder.typicode.com/posts/","limit":1}},[_c('el-button',{attrs:{"size":"small","type":"primary"}},[_vm._v("点击上传")]),_c('div',{staticClass:"el-upload__tip",attrs:{"slot":"tip"},slot:"tip"},[_vm._v("只能上传jpg/png文件，且不超过500kb")])],1)],1),_c('el-form-item',{attrs:{"label":"添加热区"}},[_c('el-button',{staticStyle:{"margin-bottom":"10px"},attrs:{"type":"primary","plain":""},on:{"click":_vm.showHit}},[_vm._v("添加热区")]),_vm._l((_vm.form.moduleData.hitData),function(item,index){return _c('div',{key:index,staticStyle:{"margin-bottom":"10px"}},[_c('el-input',{staticStyle:{"width":"700px"},attrs:{"placeholder":"请输入链接"},on:{"input":function($event){return _vm.onLink(index)}},model:{value:(item.link),callback:function ($$v) {_vm.$set(item, "link", $$v)},expression:"item.link"}},[_c('template',{slot:"prepend"},[_vm._v("热区"+_vm._s(item.index))])],2),_c('div',{staticClass:"deleteIcon",staticStyle:{"display":"inline-block"},on:{"click":function($event){return _vm.deleteLinkLine(index)}}},[_c('i',{staticClass:"el-icon-close"})])],1)})],2),_c('el-form-item',{attrs:{"label":"简介"}},[_c('el-input',{attrs:{"type":"textarea"},model:{value:(_vm.form.introtitle),callback:function ($$v) {_vm.$set(_vm.form, "introtitle", $$v)},expression:"form.introtitle"}})],1),_c('el-form-item',{attrs:{"label":"标题"}},[_c('el-input',{attrs:{"typ